.card-container{
   display: flex;
   flex-direction: column;
   background-color: #45cdea;
   border: 1px solid gray;
   border-radius: 5px;
   padding: 25px;
   cursor: pointer;
   -moz-osx-font-smoothing:grayscale;
    backface-visibility: hidden;
    transform: translate();
    transition: transform 0.25s  ease-out;


}
.card-container:hover{
    transform: scale(1.10);
}